html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #FAFAFA;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* fallback */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialicons/v29/2fcrYFNaTjcS6g4U3t-Y5UEw0lE80llgEseQY3FEmqw.woff2) format('woff2');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
	position: relative;
}

#app-content {
	max-width: 1300px;
}

#app-content * {
	margin-left: auto;
	margin-right: auto;
	max-width: 1000px;
}

#content-background {
	position: fixed;
	top: 0;
	left: 240px;
	right: 0;
	height: 300px;
	background-color: #FFC107;
}
#app-scroll-area {
	position: fixed;
	top: 0;
	left: 240px;
	right: 0;
	bottom: 0;
}

.mdc-permanent-drawer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}


.mdc-theme--dark {
	background-color: #333;
	color: #fff;
}



